body {
  font-family: sans-serif;
  color: var(--five-color);
}
:root {
  --primary--color: #171a21;
  --secondary--color: #66c0f4;
  --third--color: #1b2838;
  --four-color: #2a475e;
  --five-color: #c7d5e0;
}

#right_navbar_maincontainer {
  background-color: var(--four-color);
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: space-between;
  height: 100%;
  width: 10%;
}
#right_navbar_header > img {
  height: 100%;
  width: 100%;
}
#right_navbar_header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 5%;
}

#right_navbar_button_features {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-top: 20%;
}

#right_buttons {
  /* background-color: yellow; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80%;
}
button:hover {
  color: var(--secondary--color);
}
button {
  color: var(--five-color);
  border: 0px;
  background-color: var(--four-color);
  cursor: pointer;
}
#right_tags_container {
  height: 100%;
  /* background-color: orange; */
  justify-content: space-between;
  align-items: center;
  display: flex;
  flex-direction: column;
}

#tags_container {
  /* background-color: green; */
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  height: 60%;
  /* padding-left: 10%; */
}

#right_footer_contaienr {
  position: relative;
  font-size: 13px;
  background-color: var(--four-color);
  display: flex;
  flex-direction: column;
  /* height: 100%; */
  align-items: center;
  width: 100%;
  height: 20%;
  padding-bottom: 10%;
  /* position: absolute; */
  /* bottom: 0px; */
}
.right_footer:hover {
  color: var(--secondary--color);
}
.right_footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* position: absolute; */
  padding-top: 10%;
  cursor: pointer;
}
